.search-root {
  width: 100%;
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: 325px auto;
  grid-template-areas: 'pic content content content';
}

.search-item {
  grid-area: content;
}

.recipe-image {
  max-width: 100%;
  grid-area: pic;
  padding: 5px;
  -ms-flex-item-align: center;
      align-self: center;
}

.recipe-description {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}



